.input_div{
  padding-bottom: 13px; 
  margin:1px 35px;
}

.input_name{
  padding: 7px 0px; 
  font-size: 15px;
}

.input_text{
  padding: 5px 6px; 
  width:100%;
  outline: none;
  border:solid #ccc 1px;
  border-radius: 4px;
  font-size: 14px;
  line-height: 2;
  
}
.input_text:hover{
  box-shadow: 2px 3px 5px #ccc;
}

.message_text{
  padding: 5px 6px; 
  width:100%;
  outline: none;
  border:solid #ccc 1px;
  border-radius: 4px;
  font-size: 12px;
  line-height: 1.5;
  
  
}
.message_text:hover{
  box-shadow: 2px 3px 5px #ccc;
}
.subtitle{
  font-size: 22px;
  font-weight: 600;
}

.sendbutton{
  background-color:#00c4b8;
  width:100%;
  color:#fff;
  height: 50px;
  margin-bottom: 0;
  font-weight: 400;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none;
  border: 1px solid transparent;
  padding: 8px 10px;
  font-size: 18px;
  line-height: 1.42857143;
  border-radius: 4px;
  outline: none;
}

.sendbutton:hover{
  box-shadow: 2px 3px 5px #bbb;
}

.mlr5 {
  margin-left: 5px;
  margin-right: 5px;
}

.m10 {
